  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 19.5px;
  text-align: left;
}
.breadcrumb .breadcrumb__item a::after {
  content: "/";
  position: absolute;
  left: calc(100% + 4px);
}
.breadcrumb .breadcrumb__item.active a {
  color: #999999;
  pointer-events: none;
}
.breadcrumb .breadcrumb__item.active a::after {
  content: none;
}
.breadcrumb .breadcrumb__item-button {
  color: #D6FD52 !important;
  display: flex;
  align-items: center;
  gap: 8px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 19.5px;
  text-align: left;
}
@media (min-width: 768px) {
  .breadcrumb .breadcrumb__item-button {
    display: none;
